/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.border {
  border: 1px solid black; }

.border-bottom-solid {
  border-bottom-style: 2px solid black; }

.container {
  max-width: 1280px;
  margin: 0 auto; }

.sideBar {
  width: 20%;
  text-align: center;
  background-color: #EDF4EE;
  min-height: 100vh;
  position: relative; }
  .sideBar .logo {
    width: 150px;
    height: 40px;
    background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week5/logo-ANSWER.svg");
    background-position: center;
    background-size: auto;
    background-repeat: no-repeat; }
    .sideBar .logo a {
      width: 100%;
      height: 100%;
      display: block;
      overflow: hidden;
      text-indent: 101%;
      white-space: nowrap; }

.menu li {
  position: relative; }
  .menu li::after {
    content: "";
    width: 0px;
    height: 100%;
    background-color: #2A8356;
    position: absolute;
    top: 0;
    left: 0;
    transition: .2s; }
  .menu li.active::after {
    width: 4px; }
  .menu li.active a {
    color: #2A8356; }
  .menu li a {
    display: flex;
    align-items: center;
    color: #121212;
    font: normal normal bold 16px/24px Lato;
    transition: .5s;
    padding: 16px 32px;
    position: relative; }
    .menu li a:hover {
      color: #2A8356; }

.material-icons {
  margin-right: 16px; }

.avatar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  left: 32px;
  bottom: 40px; }
  .avatar .pic {
    width: 40px;
    height: 40px;
    background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week5/andychen.jpeg");
    background-size: cover;
    background-position: center;
    border-radius: 50%;
    margin-right: 16px; }
  .avatar .name {
    color: #121212;
    font: normal normal bold 16px/24px Lato; }
  .avatar .contact {
    color: #0000009A;
    font: normal normal normal 14px/24px Lato; }

.admin {
  width: 80%;
  padding: 32px; }
  .admin .header {
    display: flex;
    align-items: center;
    position: relative; }
    .admin .header-title {
      font: normal normal bold 24px/33px Lato;
      margin-right: 46px; }
  .admin .add-group {
    align-items: center;
    color: black; }
  .admin .page {
    padding-top: 50px; }

.page {
  margin: 64px 0 16px;
  display: flex;
  align-items: center;
  justify-content: flex-end; }
  .page li a {
    width: 32px;
    height: 32px;
    display: block;
    text-align: center;
    line-height: 32px;
    border: 1px solid #2a8356;
    margin: 0 0 -1px -1px;
    color: #121212; }
  .page li .material-icons {
    color: #2a8356; }

.header {
  justify-content: space-between; }

.new-admin {
  display: flex;
  justify-content: center;
  align-items: center; }
  .new-admin .add {
    display: block;
    color: #2A8356;
    margin-right: 5px; }
  .new-admin .add-admin {
    font: normal normal bold 16px/24px Lato; }

.new-admin:hover {
  text-decoration: none; }

.add-admin {
  color: black; }

table {
  width: 100%;
  margin-top: 48px;
  border-collapse: collapse;
  border-spacing: 0; }
  table thead tr th {
    width: 30%;
    text-align: left;
    padding-bottom: 5px;
    border-bottom: 1px solid #000;
    font: normal normal bold 16px/24px Lato; }
  table tbody td {
    font: normal normal normal 16px/24px Lato;
    padding: 20px 0 16px;
    border-bottom: 1px solid #cdddcd; }
  table .id-style {
    font: normal normal bold 16px/24px Lato;
    width: 10%; }
